$background-position: center, center
$background-repeat: no-repeat, no-repeat
$background-size: cover, cover

@font-face
    font-family: "Lato"
    src: url("../public/fonts/Lato-Bold.woff2") format("woff2")
    src: url("../public/fonts/Lato-Bold.woff") format("woff")
    font-weight: 700
    font-style: normal

@font-face
    font-family: "Lato"
    src: url("../public/fonts/Lato-Black.woff2") format("woff2")
    src: url("../public/fonts/Lato-Black.woff") format("woff")
    font-weight: 800
    font-style: normal

*,
*::before,
*::after
    padding: 0
    margin: 0
    border: 0
    box-sizing: border-box

a
    cursor: pointer
    text-decoration: none

.wrapper
    position: relative
    display: flex
    flex-direction: column
    min-height: 100vh
    width: 100%
    overflow: hidden

.container
    max-width: 1088px
    padding: 0px 80px
    margin: 0px auto
    box-sizing: content-box

.header
    z-index: 2
    display: flex
    justify-content: center
    background-color: #000
    width: 100%
    height: 80px
    flex-shrink: 0
    display: flex

    &__logo
        display: block

    &__inner
        display: flex
        align-items: center
        width: 100%
        justify-content: space-between

    &__burger-btn
        position: relative
        z-index: 2
        display: none
        width: 30px
        height: 37.79px
        border: none
        cursor: pointer
        background-color: #000

        & span
            position: absolute
            width: 22.5px
            height: 1.5px
            right: 3px
            border-radius: 5px
            background-color: #FFFFFF
            transition: opacity .25s, transform .5s
            will-change: transform

            &:nth-child(1)
                transform: translateY(-6px)

            &:nth-child(3)
                transform: translateY(6px)

.header.open .header__burger-btn span:nth-child(1)
    transform: translateY(0) rotate(45deg)

.header.open .header__burger-btn span:nth-child(2)
    opacity: 0

.header.open .header__burger-btn span:nth-child(3)
    transform: translateY(0) rotate(-45deg)

.menu

    &__list
        display: flex
        align-items: center
        list-style-type: none
        gap: 27px
        padding: 0
        margin: 0

    &__item:not(:last-child)
        margin-right: 28px

    &__link
        font-family: "Lato", sans-serif
        font-size: 18px
        line-height: 21.6px
        font-weight: 700
        color: #ffffff

        &:hover
            background: linear-gradient(90deg, #EBFF00 0%, #FF70D9 100%)
            -webkit-background-clip: text
            color: transparent

    &__cart
        &:hover
            content: url('../public/img/header/CartColor.svg')

    &__list
        display: flex
        align-items: center
        list-style-type: none
        gap: 27px
        padding: 0
        margin: 0

    &__item:not(:last-child)
        margin-right: 28px

    &__link
        font-family: "Lato", sans-serif
        font-size: 18px
        line-height: 21.6px
        font-weight: 700
        color: #ffffff

.page
    background: linear-gradient(180deg, #0D1E48 0%, #304571 100%)
    flex-grow: 1
    flex-shrink: 1
    flex-basis: auto

.discover
    background-image: url("../public/img/discover/Layout.svg"), url("../public/img/discover/MainStars.svg")
    background-position: $background-position
    background-repeat: $background-repeat
    background-size: $background-size

    &__inner
        display: flex
        align-items: center
        justify-content: center
        gap: 56px
        padding: 59px 0px 64px 0px

    &__body
        display: flex
        flex-direction: column
        gap: 22px
        margin-bottom: 22px

    &__img
        z-index: 1

    &__title
        font-family: "Lato", sans-serif
        font-size: 72px
        line-height: 72px
        font-weight: 800
        color: #ffffff

    &__space
        color: #FF70D9

    &__text
        font-family: "Lato", sans-serif
        font-size: 32px
        line-height: 120%
        font-weight: 800
        color: #ffffff

    &__endless
        color: #EBFF00

    &__button
        width: 192px
        height: 54px
        font-family: "Lato", sans-serif
        font-size: 22px
        font-weight: 700
        border-radius: 100px
        color: #000000
        background: #EBFF00
        padding: 14px 40px
        transition: all 0.3s ease 0s
        cursor: pointer

        &:hover
            filter: brightness(0.6)

.offers
    &__inner
        display: flex
        flex-direction: column
        width: 100%
        padding-top: 58px
        padding-bottom: 60px
        gap: 40px

    &__title
        font-family: "Lato", sans-serif
        font-size: 32px
        font-weight: 800
        color: #ffffff

    &__cards
        display: grid
        grid-template-columns: repeat(2, 1fr)
        width: 100%
        gap: 40px

.card
    width: 524px
    height: 350px
    border-radius: 10px
    padding: 50px 70px 35px 35px

    &:nth-child(2), &:nth-child(3)
        background-position: $background-position
        background-repeat: $background-repeat
        background-size: $background-size

    &:nth-child(2)
        background-image: url("../public/img/cards/CardLayout.svg"), url("../public/img/cards/SecondCard.svg")

    &:nth-child(3)
        background-image: url("../public/img/cards/CardLayout.svg"), url("../public/img/cards/ThirdCard.svg")

    &__inner
        display: flex
        flex-direction: column
        width: 419px
        height: 265px
        gap: 22px

    &__title
        font-family: "Lato", sans-serif
        font-size: 46px
        line-height: 100%
        font-weight: 800
        color: #ffffff
        width: 248px

    &__text
        font-family: "Lato", sans-serif
        font-size: 24px
        line-height: 120%
        font-weight: 800
        color: #ffffff

    &__button
        width: 196px
        height: 54px
        font-family: "Lato", sans-serif
        font-size: 22px
        font-weight: 700
        border-radius: 100px
        color: #EBFF00
        border: 2px solid #EBFF00
        background: inherit
        padding: 14px 40px
        transition: all 0.3s ease 0s
        cursor: pointer

        &:hover
            filter: brightness(0.6)

.doublecard
    grid-column: span 2
    border-radius: 10px
    padding: 35.86px 36px 37.14px 35.03px

    &:nth-child(1),&:nth-child(4)
        background-position: $background-position
        background-repeat: $background-repeat
        background-size: $background-size

    &:nth-child(1)
        background-image: url("../public/img/cards/CardLayout.svg"), url("../public/img/cards/FirstCard.svg")

    &:nth-child(4)
        background-image: url("../public/img/cards/CardLayout.svg"), url("../public/img/cards/FourthCard.svg")

    &__inner
        display: flex
        flex-direction: column
        width: 635px
        height: 277px
        gap: 22px

    &__title
        font-family: "Lato", sans-serif
        font-size: 74px
        line-height: 100%
        font-weight: 800
        color: #ffffff

    &__text
        font-family: "Lato", sans-serif
        font-size: 26px
        line-height: 120%
        font-weight: 800
        color: #ffffff

.journey

    &__inner
        display: flex
        flex-direction: column
        gap: 17px
        margin-bottom: 60px

    &__title
        font-family: "Lato", sans-serif
        font-size: 32px
        font-weight: 800
        color: #ffffff

    &__content
        display: none

    &__text
        font-family: "Lato", sans-serif
        font-size: 20px
        font-weight: 700
        color: #ffffff
        opacity: 60%

    &__link
        display: inline-block
        font-family: "Lato", sans-serif
        font-size: 20px
        font-weight: 800
        color: #EBFF00
        cursor: pointer

        &:hover
            filter: brightness(0.6)

#ch
    display: none

#ch:checked ~ .journey__content
    display: flex
    flex-direction: column
    gap: 17px

#ch:checked ~ label
    display: none

.footer
    background-color: #000
    padding-bottom: 19px

    &__inner
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center

    &__text
        font-family: "Lato", sans-serif
        font-size: 18px
        line-height: 28px
        font-weight: 800
        color: #EBFF00

@media (min-width: 767px) and (max-width: 1247px)
    .container
        max-width: 730px
        padding: 0px 30px

    .discover
        &__inner
            justify-content: start

        &__body
            height: 220px
            width: 411px
            margin: 100px 0

        &__title
            font-size: 48px
            line-height: 100%

        &__text
            font-size: 22px

        &__img
            position: absolute
            right: -115px

    .offers
        &__inner
            padding-top: 55px
            padding-bottom: 55px
            gap: 30px

        &__title
            font-size: 24px

        &__cards
            display: grid
            grid-template-columns: repeat(2, 1fr)
            width: 100%
            gap: 30px

    .card, .doublecard
        width: 339px
        height: 350px
        padding: 107px 32px 27px 25px

        &__inner
            width: 280px
            height: 219px

        &__title
            font-size: 40px

        &__text
            font-size: 16px

        &__button
            width: 166px
            height: 33px
            font-size: 16px
            padding: 7px 40px

    .card
        &:nth-child(2)
            background-image: url("../public/img/cards/CardLayout.svg"), url("../public/img/cards/SecondCardMini.svg")

    .doublecard
        grid-column: span 1

    .full-text
        display: none

    .journey
        margin-bottom: 53px
        &__inner
            display: flex
            flex-direction: column
            gap: 13px

        &__title
            font-size: 24px

        &__text
            font-size: 16px

        &__link
            font-size: 16px

    #ch:checked ~ .journey__content
        display: flex
        gap: 20

    .footer
        padding-bottom: 18px

@media (min-width: 320px) and (max-width: 767px)
    .container
        max-width: 320px
        padding: 0px

    .header
        &__inner
            padding: 0px 20px

    .header__burger-btn
        display: block

    .menu
        position: fixed
        right: 0
        top: 0
        width: 160px
        padding: 75px 20px 20px 20px
        background: #000
        transform: translateX(100%)
        transition: transform .5s, visibility .5s

        &__list
            display: block

        &__link
            display: block
            padding: 10px 0px

    .header.open .menu
        transform: translateX(0)
        visibility: visible

    .discover
        &__inner
            justify-content: center

        &__body
            justify-content: center
            align-items: center
            height: 158px
            width: 292px
            margin: 250px 0px 44px 0px
            gap: 17px

        &__title
            font-size: 36px
            line-height: 100%

        &__text
            font-size: 16px

        &__img
            position: absolute
            right: 20px
            top: -30px
            left: calc(50% - 170px)

        &__button
            width: 166px
            height: 33px
            font-size: 16px
            padding: 7px 40px

    .offers
        &__inner
            padding-top: 50px
            padding-bottom: 50px
            gap: 30px

        &__title
            font-size: 18px

        &__cards
            display: grid
            grid-template-columns: repeat(1, 1fr)
            width: 100%
            gap: 30px

    .card, .doublecard
        width: 320px
        height: 350px
        padding: 75px 28px 32px 25px

        &__inner
            width: 265px
            height: 239px

        &__title
            font-size: 40px

        &__text
            font-size: 16px

        &__button
            width: 166px
            height: 33px
            font-size: 16px
            padding: 7px 40px

    .card
        &:nth-child(2)
            background-image: url("../public/img/cards/CardLayout.svg"), url("../public/img/cards/SecondCardMini.svg")

    .doublecard
        grid-column: span 1

    .full-text
        display: none

    .journey
        margin-bottom: 50px
        padding:0 20px

        &__inner
            display: flex
            flex-direction: column
            gap: 20px

        &__title
            font-size: 18px

        &__text
            font-size: 14px

        &__link
            font-size: 14px

    .footer
        padding-bottom: 18px
